* {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}
a img {
  border: none;
}
blockquote {
  quotes: none;
}
blockquote:before,
blockquote:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption,
th,
td {
  text-align: left;
  font-weight: normal;
  vertical-align: middle;
}
.hide-accessible {
  width: 1px;
  height: 1px;
  overflow: hidden;
  position: absolute;
}
.hide-invisible {
  visibility: hidden;
}
.hide-display {
  display: none;
}
.skip-to-main {
  border: none;
  width: 1px;
  height: 1px;
  overflow: hidden;
  position: absolute;
}
.skip-to-main:focus {
  width: auto;
  height: auto;
  background-color: #f4f4f4;
  padding: 0.5em;
  z-index: 1;
}
.prose {
  max-width: 40rem;
  margin: 0 auto;
  padding: 0 1rem;
}
.prose__hed {
  font-weight: bold;
}
.graphic__hed {
  font-weight: 500;
}
.graphic__dek {
  margin: 0;
  margin-bottom: 1.5rem;
}
.graphic__chart {
  width: 100%;
}
.graphic__key {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.5rem;
  justify-content: flex-end;
}
.graphic__key li {
  padding: 0;
  margin-left: 0.75em;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 1;
}
.graphic__key li:before {
  content: '';
  display: inline-block;
  width: 0.8em;
  height: 0.8em;
  background-color: #282828;
  margin-right: 0.25em;
}
