#scroll {
  position: relative;
  margin: 0 auto;
  border: none;
  width: 100%;
  padding-bottom: 2rem;
}
#scroll .scroll__graphic {
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0, 0);
  bottom: auto;
}
#scroll .scroll__graphic .chart {
  position: absolute;
  width: 60%;
  background: #000;
  top: 50%;
  -moz-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  margin: 0 auto;
  margin-left: 40%;
}
#scroll .scroll__graphic .chart svg {
  overflow: visible;
}
#scroll .scroll__graphic .chart svg .axis--x .tick line {
  stroke: #fff;
}
#scroll .scroll__graphic .chart svg .axis--x .tick text {
  font-size: 0.7rem;
  font-family: 'Atlas Grotesk Web';
  fill: #fff;
  font-weight: 500;
}
#scroll .scroll__graphic .chart svg .axis--activity .tick text {
  font-size: 0.8rem;
  font-family: 'Atlas Grotesk Web';
  fill: #fff;
}
#scroll .scroll__text {
  position: relative;
  width: 30%;
  margin-left: 10%;
}
#scroll .scroll__text .step {
  margin-bottom: 30rem;
}
#scroll .scroll__text .step h4 {
  font-family: 'Atlas Grotesk Web';
  text-align: left;
}
#scroll .scroll__text .step p {
  text-align: left;
  font-size: 0.9rem;
  font-family: 'Atlas Grotesk Web';
  line-height: 1.8;
}
#scroll .is-fixed {
  position: fixed;
}
#scroll .is-bottom {
  bottom: 0;
  top: auto;
}
.axis .domain {
  display: none;
}
.axis--x line {
  stroke: #aaa;
}
.axis--activity .tick line {
  display: none;
}
.axis--activity .tick:nth-child(odd) text {
  fill: #222;
  fill: #fff;
}
.line {
